
#body 
{              
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: black;
    background-image: url('/public/ptproiect.jpg');
    background-size: cover; 
    background-position: center;
    animation: efectpr1 1.5s ease-in-out;
  }


#midcontainer {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  animation: efectpr1 3s ease-in-out;
}



input[type=email] {
  width: 250px;
  box-sizing: border-box;
  border: 3.5px solid orange;
  border-radius: 4px;
  font-size: 16px;
  background-color: white;
  background-image: url('/public/logfield1.png');
  background-position: 10px 10px; 
  background-repeat: no-repeat;
  padding: 12px 20px 12px 40px;
  transition: transform 0.4s ease-in-out, width 0.4s ease-in-out;
}


input[type=email]:focus {
  width: 360px;
  transform: translateX(55px);
  border-color: orange;
  outline: none;
  margin-left: 0; 
}


input[type=password] {
  width: 250px;
  box-sizing: border-box;
  border: 3.5px solid orange;
  border-radius: 4px;
  font-size: 16px;
  background-color: white;
  background-image: url('/public/passfield1.png');
  background-position: 10px 10px; 
  background-repeat: no-repeat;
  padding: 12px 20px 12px 40px;
  transition: transform 0.4s ease-in-out, width 0.4s ease-in-out;
}


input[type=password]:focus {
  width: 360px;
  transform: translateX(55px);
  border-color: orange;
  outline: none;
  margin-left: 0; 
}


input[type=submit]:hover
{
  cursor: pointer;
}


#notreg {
  font-size: 25px;
  color: white; 
  text-decoration-color: white; 
  font-family: "Lucida Console", "Courier New", monospace;
  max-width: 100%; 
}

#signupbutton
{
  cursor: pointer;
}
