
/* div that contains the whole main page */

#pag-welcome {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: black;
  background-image: url('/public/ptproiect.jpg'); 
  background-size: cover; 
  background-position: center;
  animation: efectpr1 1.5s ease-in-out;
}



/* the flex container from the center of the page (contains the welcome container and the 3 logos) */

#flexcon {
  display: flex;
  flex-direction: column; 
  align-items: center; 
}



/* keyframes for animations */

@keyframes efectpr {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes efectpr1 {
  from {
    opacity: 0.2;                     /* for the background transition */
  }
  to {
    opacity: 1;
  }
}



/* style for the 3 image-buttons */

.hover-text {
  display: none;
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  padding: 5px 10px;
  background-color: orange;
  color: white;
  border-radius: 3px;
  white-space: nowrap;
  font-size: 13px;
  cursor: not-allowed;
}

.icon-container {
  position: relative;
  display: inline-block;
  margin-right: 35px;
}

.icon-container:hover .hover-text {
  display: block;
}
